import axios from 'axios';
import { useState } from 'react';

function RegisterForm() {
    const [email, setEmail] = useState('');
    const [name, setName] = useState('');
    const [surname, setSurname] = useState('');
    const [phoneNumber, setPhoneNumber] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [errorText, setErrorText] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();

        if(name === '' || surname === '' || email === '' || phoneNumber === ''  || password === '' || confirmPassword === '') {
            setErrorText("    !"); 
            return;
        }

        if(password !== confirmPassword) {
            setErrorText("  !"); 
            return;
        }
        
        try {
            const response = await axios.post('https://localhost:7276/auth/register', {
                email: email,
                name: name,
                surname: surname,
                phoneNumber: phoneNumber,
                password: password,
                confirmPassword: confirmPassword
            });

            localStorage.setItem('token', response.data.token);

            window.location.href = '/tours';
        } catch (error) {
            if(error.response.status  === 409) {
                setErrorText("     !"); 
                return;
            }
            console.error('  :', error);
        }
    };

    return (
	    <form onSubmit={handleSubmit} className="auth-form">
            <input type="email" name="email" placeholder="E-mail" value={email} onChange={(e) => setEmail(e.target.value)}/>
            <input type="text" name="name" placeholder="" value={name} onChange={(e) => setName(e.target.value)} />
            <input type="text" name="surname" placeholder="" value={surname} onChange={(e) => setSurname(e.target.value)}/>
            <input type="text" name="phoneNumber" placeholder=" " value={phoneNumber} onChange={(e) => setPhoneNumber(e.target.value)}/>
            <input type="password" name="password" placeholder="" value={password} onChange={(e) => setPassword(e.target.value)}/>
            <input type="password" name="confirmPassword" placeholder=" " value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value)}/>
            <div id="errorMessage" style={{marginTop: '15px'}}>{errorText}</div>
            <button type="submit"></button>
        </form>
  	);
}

export default RegisterForm;